import React, { Component } from 'react';
import { Accordion } from 'react-bootstrap';
import SVG from 'react-inlinesvg'
import { baseUrl } from '../../../../Assets/AssetsHelper';
import { getPublishList } from '../../../../Services/Actions/publishActions';
import Filters from '../../WebEmbed/Components/Filters';

class SocialPublishAside extends Component {
    constructor() {
        super();
        this.state = {
            showModal: false,
            campaignType: 'HashtagCampaign',
            publishList: []
        };
    }
    componentDidMount() {
        this.onLoadPublish()
    }
    onLoadPublish = () => {
        var self = this
        getPublishList().then((loadReponse) => {
            self.setState({ publishList: loadReponse.data })
        })
    }

    campaignTypeFun = event => this.props.campaignTypeCallback(event)

    render() {
        const { setValue } = this.state
        const { filterApply, state, genratedCodeModal, onClickToDelete, onResetFilter, activeFilter, history, onChangeName, onClickConnectWith, onClickmediaSize, onClickPublishType, onChangeDescription, onSelectActive } = this.props
        return <><div className='filter_wrapper'>
            <Accordion defaultActiveKey={1} activeKey={state.activeKey} onSelect={onSelectActive(1) ? onSelectActive(1) : null}>
                <Accordion.Item eventKey={1} className="pb-3">
                    <Accordion.Header className="add_fillter_btn">
                        <i className="ri-add-line plus_symbol me-1"></i>
                        <i className="ri-subtract-line minus_symbol me-1"></i>
                        <span className="fs-9 fw-medium">Choose Filter</span>
                    </Accordion.Header>
                    <Accordion.Body className="border rounded-1 mt-1 choose_filter">
                        <div className={`d-flex align-items-center border-bottom py-2 cursor-pointer ${Object.keys(state.chooseFilter).includes("collection") ? `d-none disabled` : ``}`} onClick={filterApply("collection")}>
                            <div className="symbol symbol-28 bg-light me-2">
                                <div className="symbol-label">
                                    <i className="ri-folder-4-line icon-7 text-gray-800"></i>
                                </div>
                            </div>
                            <div className="d-flex flex-column">
                                <p className={`fs-9 fw-medium mb-0 text-gray-800`}>Collection</p>
                                <span className="fs-10 text-gray-500">Select the preferred collection</span>
                            </div>
                        </div>
                        <div className={`d-flex align-items-center border-bottom py-2 cursor-pointer ${Object.keys(state.chooseFilter).includes("source") ? `d-none disabled` : ``}`} onClick={filterApply("source")}>
                            <div className="symbol symbol-28 bg-light me-2">
                                <div className="symbol-label">
                                    <span className="svg-icon svg-icon-7 text-gray-800 me-0">
                                        <SVG src={baseUrl('media/icons/svg-icons/network.svg')} height={18} width={18} />
                                    </span>
                                </div>
                            </div>
                            <div className="d-flex flex-column">
                                <p className={`fs-9 fw-medium mb-0 text-gray-800`}>Network</p>
                                <span className="fs-10 text-gray-500">Choose the required network</span>
                            </div>
                        </div>
                        <div className={`d-flex align-items-center border-bottom py-2 cursor-pointer  ${Object.keys(state.chooseFilter).includes("connection") ? `d-none disabled` : ``}`} onClick={filterApply("connection")}>
                            <div className="symbol symbol-28 bg-light me-2">
                                <div className="symbol-label">
                                    <span className="svg-icon svg-icon-7 text-gray-800 me-0">
                                        <SVG src={baseUrl('media/icons/svg-icons/network-manager.svg')} height={18} width={18} />
                                    </span>
                                </div>
                            </div>
                            <div className="d-flex flex-column">
                                <p className={`fs-9 fw-medium mb-0 text-gray-800`}>Connections</p>
                                <span className="fs-10 text-gray-500">Select the connection type</span>
                            </div>
                        </div>
                        <div className={`d-flex align-items-center border-bottom py-2 cursor-pointer  ${Object.keys(state.chooseFilter).includes("assetsType") ? `d-none disabled` : ``}`} onClick={filterApply("assetsType")}>
                            <div className="symbol symbol-28 bg-light me-2">
                                <div className="symbol-label">
                                    <i className="ri-image-line icon-7 text-gray-800"></i>
                                </div>
                            </div>
                            <div className="d-flex flex-column">
                                <p className={`fs-9 fw-medium mb-0 text-gray-800`}>Assets Type</p>
                                <span className="fs-10 text-gray-500">Choose the type of asset</span>
                            </div>
                        </div>
                        <div className={`d-flex align-items-center border-bottom py-2 cursor-pointer ${Object.keys(state.chooseFilter).includes("tags") ? `d-none disabled` : ``}`} onClick={filterApply("tags")}>
                            <div className="symbol symbol-28 bg-light me-2">
                                <div className="symbol-label">
                                    <i className="ri-price-tag-3-line icon-7 text-gray-800"></i>
                                </div>
                            </div>
                            <div className="d-flex flex-column">
                                <p className={`fs-9 fw-medium mb-0 text-gray-800`}>Tag</p>
                                <span className="fs-10 text-gray-500">Select the preferred tag</span>
                            </div>
                        </div>
                        <div className={`d-flex align-items-center py-2 cursor-pointer ${Object.keys(state.chooseFilter).includes("ugcrights") ? `d-none disabled` : ``}`} onClick={filterApply("ugcrights")}>
                            <div className="symbol symbol-28 bg-light me-2">
                                <div className="symbol-label">
                                    <span className="svg-icon svg-icon-7 text-gray-800 me-0">
                                        <SVG src={baseUrl('media/icons/svg-icons/rights-approved-line.svg')} height={18} width={18} />
                                    </span>
                                </div>
                            </div>
                            <div className="d-flex flex-column">
                                <p className={`fs-9 fw-medium mb-0 text-gray-800`}>UGC Rights</p>
                                <span className="fs-10 text-gray-500">Select the preferred ugc rights status</span>
                            </div>
                        </div>


                    </Accordion.Body>
                </Accordion.Item>
            </Accordion>
            {
                state.chooseFilter && Object.keys(state.chooseFilter).map((filterKey, indexFilter) => <Filters filterKey={filterKey} {...this.props} indexFilter={indexFilter} isLast={Object.keys(state.chooseFilter)[Object.keys(state.chooseFilter).length - 1]} />)
            }
            <Accordion defaultActiveKey={''}>
                <label className="text-gray-800 mb-2">Select Network</label>
                <Accordion.Item eventKey="7" className="pb-3">
                    <Accordion.Header className="w-100">
                        <span className="fs-9 fw-medium">{state.connectWith == 1 ? `Twitter` : state.connectWith == 18 ? `Instagram` : state.connectWith == 3 ? `Facebook` : `Choose Preferred Social`}</span>
                    </Accordion.Header>
                    <Accordion.Body className="border rounded-1 mt-1">
                        <div className="">
                            <div className="dropdown-item px-0 border-bottom rounded-0" >
                                <label className="form-check flex-grow-1 overflow-hidden align-items-center" htmlFor={`twitter`}>
                                    <span className="symbol symbol-22">
                                        <img className="img-fluid h-100" src={baseUrl(`media/icons/social-icons-square/twitter.svg`)} height={20} width={20} alt={'twitter'} />
                                    </span>
                                    <span className="form-check-label text-gray-800 text-ellipsis">Twitter</span>
                                </label>
                                <input className="form-check-input flex-shrink-0" type="radio" name="network" checked={state.connectWith == 1 ? true : false} id={`twitter`} onClick={onClickConnectWith(1)} />
                            </div>
                            <div className="dropdown-item px-0 border-bottom rounded-0" >
                                <label className="form-check flex-grow-1 overflow-hidden align-items-center" htmlFor={`instagram`}>
                                    <span className="symbol symbol-22">
                                        <img className="img-fluid h-100" src={baseUrl(`media/icons/social-icons-square/instagram.svg`)} height={20} width={20} alt={'instagram'} />
                                    </span>
                                    <span className="form-check-label text-gray-800 text-ellipsis">Instagram</span>
                                </label>
                                <input className="form-check-input flex-shrink-0" type="radio" name="network" checked={state.connectWith == 18 ? true : false} id={`instagram`} onClick={onClickConnectWith(18)} />
                            </div>
                            <div className="dropdown-item px-0 rounded-0" >
                                <label className="form-check flex-grow-1 overflow-hidden align-items-center" htmlFor={`facebook`}>
                                    <span className="symbol symbol-22">
                                        <img className="img-fluid h-100" src={baseUrl(`media/icons/social-icons-square/facebook.svg`)} height={20} width={20} alt={'twitter'} />
                                    </span>
                                    <span className="form-check-label text-gray-800 text-ellipsis">Facebook</span>
                                </label>
                                <input className="form-check-input flex-shrink-0" type="radio" name="network" checked={state.connectWith == 3 ? true : false} id={`facebook`} onClick={onClickConnectWith(3)} />
                            </div>
                            <div className="dropdown-item px-0 rounded-0 d-none" >
                                <label className="form-check flex-grow-1 overflow-hidden align-items-center" htmlFor={`tiktok`}>
                                    <span className="symbol symbol-22">
                                        <img className="img-fluid h-100" src={baseUrl(`media/icons/social-icons-square/tiktok.svg`)} height={20} width={20} alt={'twitter'} />
                                    </span>
                                    <span className="form-check-label text-gray-800 text-ellipsis">TikTok</span>
                                </label>
                                <input className="form-check-input flex-shrink-0" type="radio" name="network" value="" id={`tiktok`} />
                            </div>
                        </div>
                    </Accordion.Body>
                </Accordion.Item>
                <label className="text-gray-800 mb-2">Asset Type</label>
                <Accordion.Item eventKey="8" className="pb-3">
                    <Accordion.Header>
                        <span className="fs-9 fw-medium">{state.publishType == 1 ? `Single Image or Video` : `Choose Content Type`} </span>
                    </Accordion.Header>
                    <Accordion.Body className="border rounded-1 mt-1">
                        <div className="navi-item d-flex align-items-center py-2 cursor-pointer cursor-pointer">
                            <label className="form-check flex-grow-1 align-items-center" htmlFor="singleMedia">
                                <input className="form-check-input" type="radio" name="publishType" value="singleMedia" id="singleMedia" checked={state.publishType == 1 ? true : false} onClick={onClickPublishType(1)} />
                                <div className="navi-text ms-2">
                                    <p className="fs-9 fw-medium mb-0">Single Image or Video</p>
                                </div>
                            </label>

                        </div>
                        <div className="navi-item d-flex align-items-center py-2 cursor-pointer cursor-pointer d-none">
                            <label className="form-check flex-grow-1 align-items-center" htmlFor="carouselPost">
                                <input className="form-check-input" type="radio" name="publishType" value="carouselPost" id="carouselPost" checked={state.publishType == 2 ? true : false} onClick={onClickPublishType(2)} />
                                <div className="navi-text ms-2">
                                    <p className="fs-9 fw-medium mb-0">Carousel</p>
                                </div>
                            </label>
                        </div>
                    </Accordion.Body>
                </Accordion.Item>

                <Accordion.Item eventKey="9" className="pb-3">
                    <Accordion.Header>
                        <span className="fs-9 fw-medium">{state.mediaSize == 1 ? `1:1` : state.mediaSize == 16 ? `16:9` : state.mediaSize == 0 ? `Default Size` : `Media Size`}</span>
                    </Accordion.Header>
                    <Accordion.Body className="border rounded-1 mt-1">
                        <div className="navi-item d-flex align-items-center border-bottom py-2 cursor-pointer">
                            <label className="form-check flex-grow-1 align-items-center" htmlFor="1x1">
                                <input className="form-check-input" type="radio" name="imageSize" value="1x1" id="1x1" onClick={onClickmediaSize(1)} checked={state.mediaSize == 1 ? true : false} />
                                <div className="navi-text ms-2">
                                    <p className="fs-9 fw-medium mb-0">1:1</p>
                                </div>
                            </label>
                        </div>
                        <div className="navi-item d-flex align-items-center border-bottom py-2 cursor-pointer">
                            <label className="form-check flex-grow-1 align-items-center" htmlFor="16x9">
                                <input className="form-check-input" type="radio" name="imageSize" value="16x9" id="16x9" onClick={onClickmediaSize(16)} checked={state.mediaSize == 16 ? true : false} />
                                <div className="navi-text ms-2">
                                    <p className="fs-9 fw-medium mb-0">16:9</p>
                                </div>
                            </label>
                        </div>
                        <div className="navi-item d-flex align-items-center py-2 cursor-pointer">
                            <label className="form-check flex-grow-1 align-items-center" htmlFor="defaultSize">
                                <input className="form-check-input" type="radio" name="imageSize" value="defaultSize" id="defaultSize" onClick={onClickmediaSize(0)} checked={state.mediaSize == 0 ? true : false} />
                                <div className="navi-text ms-2">
                                    <p className="fs-9 fw-medium mb-0">Default Size</p>
                                </div>
                            </label>
                        </div>
                    </Accordion.Body>
                </Accordion.Item>
                <label className="text-gray-800 mb-2">Description</label>
                <div className="w-100 mb-2">
                    <textarea className="form-control textarea-min-row-5 fs-9" row={5} onChange={onChangeDescription} value={state.selectedPost ? state.selectedPost.content : null}></textarea>
                </div>
            </Accordion>

        </div>
            {/* <div className='filter_actions_'>

                <Button variant='link' className='w-100 text-decoration-none fw-medium' onClick={onResetFilter}>Back</Button>
            </div> */}

        </>
    }
}

export default SocialPublishAside;

